import React, { useState } from "react";
import Box from "@material-ui/core/Box";

const TaskOverlay = (children, menuComponents) => {
  const [isHovered, setHover] = useState(false);
  return (
    <Box
      onMouseEnter={() => {
        setHover(true);
      }}
      onMouseLeave={() => setHover(false)}
      borderColor={isHovered ? "primary.main" : null}
      position={"relative"}
    >
      {isHovered && (
        <Box
          position={"absolute"}
          top={"-20px"}
          right={"20px"}
          borderColor={"primary.main"}
        >
          {menuComponents}
        </Box>
      )}
      <Box m={isHovered ? "-1px" : null}>{children}</Box>
    </Box>
  );
};

export default TaskOverlay;
